import React, { Component } from 'react';
import Link from 'next/link'


//CSS

const containerNav = {
  fontFamily: 'Muli',
  backgroundColor: '#fffff',
  margin: '0',
  padding: '0',
  width: '100%',
  height: '35px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  webkitBoxShadow: '0px 2px 5px 0px rgba(0,0,0,0.3)',
  mozBoxShadow: '0px 2px 5px 0px rgba(0,0,0,0.3)',
  boxShadow: '0px 2px 5px 0px rgba(0,0,0,0.3)'
}

const links = {
  color: 'black',
  textDecoration: 'none',
  marginRight: '10px',
  marginLeft: '10px'
}


class Nav extends Component{
  render(){
    return(
      <div style={containerNav}>
        <Link href="/">
          <a style={links}>Home</a>
        </Link>
        <Link href="/test">
          <a style={links}>Test</a>
        </Link>
      </div>
    )
  }
}


export default Nav;



